times: 0,
productIntro: '',
productType: 0,
isPing: false,
isPingSinger: '',
pingGrounpNum: 0,
hlBuyLi2: 'hl-buy-li2',
isActive: false,
isKan: false,
isKanText: '',
isKanHelp: '',
isType: false,
recordList: [],
allList: [],
allListMore: [],
vGroupOrderGuid: '',
vGroupProductGuid: '',
config: {},
showMode: false,
showShare: false,
shareStr: '',
heliang: '',
isWeixin: true,
isShowKan: false,
isShowPin: false,
noDelivery: true,
hours: 24,
beginDate: 0,
endDate: 0,
inventory: 0,  // 
maxGroupSize: 0,
h: 0,
m: 0,
s: 0,
countDown: '',
countSecs: 0,
allHours: 0,
timer1: null,
